.about {
  &__title {
    margin: 0 0 48px;
    font-weight: bold;
    font-size: 36px;
    line-height: 36px;
    align-items: center;
    letter-spacing: -1.5px;
    color: $colorBlack;

    @include onDesktop {
      font-size: 48px;
      line-height: 48px;
    }
  }

  &__title-link {
    display: inline-block;
  }

  &__subtitle {
    display: inline-block;
    margin-bottom: 16px;

    font-weight: bold;
    font-size: 24px;
    line-height: 30px;
  }

  &__paragraph {
    margin: 0 0 27px;

    &--important {
      font-weight: bold;
    }
  }
}
