.menu {
  background-color: $colorBlue;
  height: 100vh;
  box-sizing: border-box;
  overflow-y: auto;
  z-index: 1;
  
  &__content {
    box-sizing: border-box;
    height: 100vh;
    padding: 24px 0;
  }

  &__top {
    margin-bottom: 48px;
  }

  &__list {
    margin: 0 0 48px;
    padding: 0;
    list-style: none;
  }

  &__item {
    &:not(:last-child) {
      margin-bottom: 24px;
    }
  }

  &__link {
    display: inline-block;
    padding-bottom: 8px;
    border-bottom: 1px solid transparent;
    
    color: $colorWhite;
    text-decoration: none;
    font-weight: bold;
    font-size: 22px;
    line-height: 22px;
    letter-spacing: 2px;
    text-transform: uppercase;

    transition: border-color $animationTime;

    &:hover {
      border-color: $colorWhite;
    }
  }

  &__call {
    display: block;
    width: max-content;
    
    font-weight: bold;
    font-size: 12px;
    line-height: 16px;
    letter-spacing: 2px;
    text-transform: uppercase;
    color: $colorWhite;
    text-decoration: none;

    &--phone {
      margin-bottom: 16px;
    }

    &--action {
      padding-bottom: 8px;
      border-bottom: 1px solid $colorWhite;
    }
  }
}
